body {
  margin: 0;
  background-color: #c0a9bd;
  font-family: sans-serif;
}

h1 {
  margin: 0;
  margin-bottom: 15px;
  color: #c0a9bd;
}

.task-container {
  width: 350px;
  margin: auto;
  margin-top: 30px;
  text-align: center;
}

.input-section {
  background-color: #f4f2f3;
  padding: 18px;
  border-radius: 10px;
}

.input-section input {
  border-radius: 5px;
  border: 1px solid #c0a9bd;
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
}

input::placeholder {
  color: #c0a9bd;
}

ul {
  padding: 0;
  text-align: left;
}

ul li {
  list-style: none;
  background-color: #f4f2f3;
  padding: 15px;
  border-radius: 5px;
  margin-bottom: 10px;
  color: #c0a9bd;
  display: flex;
}

ul li i {
  text-align: right;
  flex: 1;
}
